.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-color: #fff #fff rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 70px auto;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  transform-origin: center;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #ff3d00 #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:before {
  width: 32px;
  height: 32px;
  border-color: #fff #fff rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation: rotation 1.5s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.breed-select {
  width: 100%;
  cursor: pointer;
  background-color: #72c6a0;
  border: 1px solid #d87474;
  border-radius: 6px;
  padding: 10px;
  font-size: 16px;
  display: block;
}

.cat-info {
  background-color: #dbccde;
  border-radius: 5px;
}

